#landing {
    height: 100vh;
    width: 100%;
    background: #0a0a0a;
    overflow: visible;
}

.mainHeader {
    height:100vh;
    position: absolute;
    font-size: 50px;
    margin-left: 10vw;
       display: flex;
       justify-content: center;
       flex-direction: column;
       /* transform: rotate(90deg); */
       line-height: 2em;
       pointer-events: none;
}
#outlineHead {
    font-family: 'MADE TOMMY OUTLINE';
    font-weight: 400;
    /* color: #e9322e; */
    color: rgba(255, 255, 255, 0.731);
    margin: 0;
}
#fillHead {
    font-family: 'MADE TOMMY';
    font-weight: 500;
    color: rgba(255, 255, 255, 0.895);
    margin: 0;
}

